// js аккордион-фильтр

$(function () {

    $('.js-accordion-btn').click(function () {

        var dropDown = $(this).parent().find('.js-accordion-content');

        $(this).toggleClass('active');
        dropDown.stop(false, true).slideToggle();
    });

})

/*Показать больше*/

$(".content-block").each(function () {
    let more = $(this).find(".show-more");
    let hide = $(this).find(".hide-content");
    hide.hide();
    more.click(function () {
        hide.slideToggle();
        more.text(more.text() == "Скрыть" ? "Показать еще 15" : "Скрыть");
    });
});

